export default {
	data() {
		return {
			datas: '',
			goods_name: '',
		}
	},
	onLoad(options) {
		var that = this;
		var datas = uni.getStorageSync('record_user_list');
		if (datas) {
			that.datas = datas;
			that.goods_name = uni.getStorageSync('goods_name');
		} else {
			this.$http.post("pintuan/show", {
				pintuan_id: options.pintuan_home_id,
				action: 'self'
			}).then((res) => {
				that.datas = res.data.record_user_list;
				that.goods_name = res.data.goods_name;
			})
		}

	},
	methods: {

		jumpTo(e) {
			var jumptype = e.currentTarget.dataset.jumptype;
			var url = e.currentTarget.dataset.url;
			var closepage = e.currentTarget.dataset.closepage;
			this.jumpFunction(jumptype, url, closepage);
		},
	}
}
